@charset "UTF-8";
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

:root {
  --heihgt: 100vh; }

html,
body {
  height: 100%;
  background-color: #1a1a1a;
  position: relative; }

body {
  font-family: "Onset", serif;
  color: #fff; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.main {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container {
  max-width: 1280px;
  width: 100%;
  padding: 0 40px 0;
  margin: 0 auto; }
  @media screen and (max-width: 768px) {
    .container {
      max-width: 1260;
      padding: 0 30px 0; } }
  @media screen and (max-width: 500px) {
    .container {
      max-width: 1240;
      padding: 0 20px 0; } }
  @media screen and (max-width: 360px) {
    .container {
      max-width: 1220;
      padding: 0 10px 0; } }

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  border: 1px solid black;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

.section {
  padding: 200px 0; }

.run-title {
  margin-bottom: 80px; }

@font-face {
  font-family: "Onset";
  src: url(../fonts/OnestRegular.woff) format("woff"), url(../fonts/OnestRegular.woff2) format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Onset";
  src: url(../fonts/OnestBold.woff) format("woff"), url(../fonts/OnestBold.woff2) format("woff2");
  font-display: swap;
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "Onset";
  src: url(../fonts/OnestLight.woff) format("woff"), url(../fonts/OnestLight.woff2) format("woff2");
  font-display: swap;
  font-weight: light;
  font-style: normal; }

@font-face {
  font-family: "Onset";
  src: url(../fonts/OnestMedium.woff) format("woff"), url(../fonts/OnestMedium.woff2) format("woff2");
  font-display: swap;
  font-weight: medium;
  font-style: normal; }

@font-face {
  font-family: "Pf-Scandal-Pro";
  src: url(../fonts/PF-Scandal-Pro.woff) format("woff"), url(../fonts/PF-Scandal-Pro.woff2) format("woff2");
  font-display: swap;
  font-weight: black;
  font-style: normal; }

.header {
  padding: 60px 0 20px;
  border-bottom: 1px solid #fff;
  position: fixed;
  z-index: 5;
  width: 100%;
  background-color: #1a1a1a; }
  .header__container {
    display: flex;
    align-items: center; }
  .header__logo {
    display: flex;
    align-items: center; }
    @media screen and (max-width: 768px) {
      .header__logo svg {
        height: 18px; } }
    .header__logo svg path {
      transition: 0.3s all; }
    .header__logo svg:hover path {
      fill: #aa4334; }
  .header__nav {
    display: flex;
    margin-left: auto; }
  .header__anchors {
    display: flex;
    gap: 0 45px;
    margin-right: 58px; }
    @media screen and (max-width: 1024px) {
      .header__anchors {
        display: none; } }
  .header__social {
    display: flex;
    gap: 0 27px; }
    @media screen and (max-width: 650px) {
      .header__social {
        display: none; } }
  .header__burger-btn {
    display: none;
    width: 40px;
    height: 20px;
    position: relative; }
    @media screen and (max-width: 1024px) {
      .header__burger-btn {
        display: block;
        margin-left: 37px; } }
    .header__burger-btn span {
      display: inline-block;
      height: 3px;
      width: 100%;
      background-color: #fff;
      position: absolute;
      left: 0; }
      .header__burger-btn span:first-child {
        top: 0; }
      .header__burger-btn span:nth-child(2) {
        top: 50%;
        transform: translateY(-50%); }
      .header__burger-btn span:last-child {
        bottom: 0; }

.mobile-menu {
  max-width: 415px;
  width: 100%;
  background-color: #1a1a1a;
  height: 100%;
  border-left: 1px solid #fff;
  right: 0;
  z-index: 2;
  padding: 50px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100;
  visibility: hidden;
  transform: translateX(100%);
  transition: all cubic-bezier(0.81, 0.16, 0.41, 0.98) 0.5s; }
  @media screen and (max-width: 500px) {
    .mobile-menu {
      max-width: 280px; } }
  .mobile-menu .anchors,
  .mobile-menu .social {
    display: flex;
    flex-direction: column;
    gap: 30px 0; }
  .mobile-menu .anchors {
    margin: 30px 0; }
  .mobile-menu.active {
    visibility: visible;
    transform: translateX(0); }
  .mobile-menu .close:hover svg path {
    fill: #aa4334; }
  .mobile-menu .close svg path {
    fill: #fff;
    transition: 0.3s all; }

.overlay {
  width: 100%;
  min-height: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 89;
  background: rgba(23, 23, 23, 0.7);
  visibility: hidden;
  opacity: 0;
  pointer-events: none; }
  .overlay.active {
    visibility: visible;
    opacity: 1;
    pointer-events: visible; }

.anchors {
  gap: 0 45px;
  margin-right: 58px; }
  .anchors-link {
    font-size: 14px;
    font-weight: 300;
    padding: 10px 0;
    display: inline-flex;
    align-items: center;
    transition: 0.3s all;
    position: relative; }
    .anchors-link::after {
      content: "";
      position: absolute;
      height: 2px;
      width: 0;
      background-color: #aa4334;
      bottom: 0;
      transition: 0.3s all; }
    .anchors-link:hover {
      color: #aa4334; }
      .anchors-link:hover::after {
        width: 100%; }

.social {
  gap: 0 27px; }
  .social-link {
    text-transform: uppercase;
    padding: 10px 0;
    display: inline-flex;
    align-items: center;
    transition: 0.3s all;
    font-size: 18px; }
    @media screen and (max-width: 1024px) {
      .social-link {
        font-size: 14px; } }
    .social-link:hover {
      color: #aa4334; }

.promo {
  position: relative;
  max-width: 100vw;
  margin-top: 123px;
  z-index: 1; }
  @media screen and (max-width: 1440px) {
    .promo {
      margin-top: 118px; } }
  @media screen and (max-width: 1024px) {
    .promo {
      margin-top: 120px; } }
  @media screen and (max-width: 768px) {
    .promo {
      margin-top: 101px; } }
  .promo::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.8) 0.15%, rgba(0, 0, 0, 0.63) 29.44%, rgba(0, 0, 0, 0) 78.65%); }
    @media screen and (max-width: 768px) {
      .promo::after {
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.8) 0.17%, rgba(0, 0, 0, 0.63) 57.14%, rgba(0, 0, 0, 0) 105.68%); } }
  .promo__bg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* Масштабирует изображение, чтобы заполнить всю область */
    object-position: center bottom;
    z-index: 2; }
  .promo__container {
    top: 0;
    left: 0;
    position: absolute;
    z-index: 3;
    left: 50%;
    transform: translateX(-50%);
    top: 18%; }
    @media screen and (max-width: 1024px) {
      .promo__container {
        top: 10%; } }
    @media screen and (max-width: 768px) {
      .promo__container {
        top: 20%; } }
  .promo__title {
    z-index: 4;
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase; }
    .promo__title span {
      text-transform: none;
      font-family: "Pf-Scandal-Pro";
      font-weight: 900;
      font-size: 40px; }
    @media screen and (max-width: 1440px) {
      .promo__title {
        font-size: 50px; } }
    @media screen and (max-width: 768px) {
      .promo__title {
        font-size: 38px; } }
    @media screen and (max-width: 500px) {
      .promo__title {
        font-size: 30px; }
        .promo__title span {
          font-size: 28px; } }
    @media screen and (max-width: 400px) {
      .promo__title {
        font-size: 27px; }
        .promo__title span {
          font-size: 24px;
          line-height: 1.5; } }
